<template>
  <div class="tencentQuestion">

  </div>
</template>

<script>
import { getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({ getTokenQuery: getSearch() })
    next()
  },
  created () {
    const { urlType } = getSearch()
    console.log(urlType)
    const mid = this.$store.state.user.userInfo.id
    let questionUrl = `https://wj.qq.com/s2/8928062/7451/?openid=mid${mid}`
    if (urlType === '2') {
      questionUrl = `https://wj.qq.com/s2/8905000/bd18/?openid=mid${mid}`
    }
    if (urlType === '3') {
      questionUrl = `https://wj.qq.com/s2/8991498/3e4e/?openid=mid${mid}`
    }
    location.href = questionUrl
  }
}
</script>
